import React from "react";
import GaugeChart from "react-gauge-chart";

const TempChart = ({ machine }) => {
    const temperature = machine.map((data) => data.ProcessTemperature);
    const toolwear = machine.map((data) => data.ToolWear);
    const torque = machine.map((data) => data.Torque);

    return (
        <GaugeChart
            id="gauge-chart"
            nrOfLevels={temperature}
            arcsLength={[temperature, toolwear, torque]}
            colors={['#96C2F2', '#F2A7C0', '#F1C2A4']}
            percent={temperature / 1000}
            arcPadding={0.02}
            needleColor='#96C2F2'
            textColor='#0000ff'
        />
    );
};

export default TempChart;
